/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@500&display=swap");
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

img {
  max-width: 100%;
  height: auto;
}

*, *::before, *::after {
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

body {
  color: #424242;
  line-height: 1;
  font-family: "Noto Sans TC", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 1rem;
}

a {
  color: #424242;
}

.wrap {
  max-width: 1024px;
  margin: 0 auto;
}

.input-area {
  background-color: #424242;
  padding-top: 74px;
  padding-bottom: 74px;
}
.input-area .wrap {
  display: flex;
  justify-content: center;
  align-items: center;
}
.input-area__inputs {
  margin-left: 97px;
  margin-right: 53px;
}
.input-area__inputs label {
  color: #FFD366;
  display: block;
  font-size: 1.125rem;
  margin-bottom: 8px;
}
.input-area__inputs input {
  display: block;
  color: white;
  font-size: 1.5rem;
  width: 250px;
  padding: 0.5rem 1rem;
  background: none;
  outline: none;
  border: 2px solid #FFD366;
  border-radius: 10px;
}
.input-area__inputs input:first-of-type {
  margin-bottom: 1.5rem;
}
.input-area__inputs input::placeholder {
  color: white;
  opacity: 0.5;
}
.input-area__result.active .result-btn {
  display: none;
}
.input-area__result.active .result {
  display: flex;
}
.input-area__result .result-btn {
  cursor: pointer;
  width: 120px;
  height: 120px;
  border-radius: 50%;
  background-color: #FFD366;
  border: none;
  font-size: 1.5rem;
}
.input-area__result .result-btn:hover {
  box-shadow: 0 1px 6px 3px rgba(255, 196, 50, 0.64);
}
.input-area__result .result-btn:active {
  background-color: #DEA821;
}
.input-area__result .result {
  display: none;
  justify-content: center;
  align-items: center;
  position: relative;
}
.input-area__result .result-circle {
  position: relative;
  width: 123px;
  height: 123px;
  border-radius: 50%;
  border: 6px solid;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-right: 18px;
}
.input-area__result .result-circle.underWeight, .input-area__result .result-circle.underWeight + .result-text {
  color: #31BAF9;
  border-color: #31BAF9;
}
.input-area__result .result-circle.underWeight .loop-icon, .input-area__result .result-circle.underWeight + .result-text .loop-icon {
  background-color: #31BAF9;
}
.input-area__result .result-circle.normal, .input-area__result .result-circle.normal + .result-text {
  color: #86D73F;
  border-color: #86D73F;
}
.input-area__result .result-circle.normal .loop-icon, .input-area__result .result-circle.normal + .result-text .loop-icon {
  background-color: #86D73F;
}
.input-area__result .result-circle.overWeight, .input-area__result .result-circle.overWeight + .result-text {
  color: #FF982D;
  border-color: #FF982D;
}
.input-area__result .result-circle.overWeight .loop-icon, .input-area__result .result-circle.overWeight + .result-text .loop-icon {
  background-color: #FF982D;
}
.input-area__result .result-circle.mildObesity, .input-area__result .result-circle.mildObesity + .result-text, .input-area__result .result-circle.obesity, .input-area__result .result-circle.obesity + .result-text {
  color: #FF6C03;
  border-color: #FF6C03;
}
.input-area__result .result-circle.mildObesity .loop-icon, .input-area__result .result-circle.mildObesity + .result-text .loop-icon, .input-area__result .result-circle.obesity .loop-icon, .input-area__result .result-circle.obesity + .result-text .loop-icon {
  background-color: #FF6C03;
}
.input-area__result .result-circle.severeObesity, .input-area__result .result-circle.severeObesity + .result-text {
  color: #FF1200;
  border-color: #FF1200;
}
.input-area__result .result-circle.severeObesity .loop-icon, .input-area__result .result-circle.severeObesity + .result-text .loop-icon {
  background-color: #FF1200;
}
.input-area__result .result-circle .result-number {
  font-size: 2rem;
  margin-bottom: 4px;
}
.input-area__result .result-circle .loop-icon {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  border: 3px solid #424242;
  border-radius: 50%;
  right: -5%;
  bottom: 5%;
}
.input-area__result .result-text {
  font-size: 2rem;
  position: absolute;
  right: -120px;
  top: 50%;
  transform: translateY(-50%);
}

.outcome-area {
  background-color: whitesmoke;
  padding-top: 48px;
  padding-bottom: 74px;
  min-height: calc(100vh - 411px);
}
.outcome-area .title {
  text-align: center;
  font-size: 1.5rem;
  margin-bottom: 36px;
}
.outcome-area .list-item {
  width: 624px;
  margin: 0 auto;
  padding: 21px 12px 21px 19px;
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  margin-bottom: 1rem;
}
.outcome-area .list-item .status-bar {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 7px;
}
.outcome-area .list-item .status-bar.underWeight {
  background-color: #31BAF9;
}
.outcome-area .list-item .status-bar.normal {
  background-color: #86D73F;
}
.outcome-area .list-item .status-bar.overWeight {
  background-color: #FF982D;
}
.outcome-area .list-item .status-bar.mildObesity, .outcome-area .list-item .status-bar.obesity {
  background-color: #FF6C03;
}
.outcome-area .list-item .status-bar.severeObesity {
  background-color: #FF1200;
}
.outcome-area .list-item .status {
  font-size: 1.25rem;
}
.outcome-area .list-item .number {
  display: flex;
  align-items: center;
}
.outcome-area .list-item .number p:first-child {
  font-size: 0.75rem;
  margin-right: 8px;
}
.outcome-area .list-item .number p:last-child {
  font-size: 1.25rem;
}

.footer {
  padding: 18px 0;
  background-color: #FFD366;
}
.footer img {
  display: block;
  margin: 0 auto;
}